.icon-button {
    padding: 0px 5px;
    background-color: transparent;
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    float: right;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
}

div textarea{
    width: 100%;
    border: none;
    overflow: auto;
    outline: none;
    box-shadow: none;
    resize: none; 
}

div.standalone {
    clear: both;
    margin-top: 0.5rem !important;
}

